html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

.box-article {
  width: 250px;
  height: 380px;
}
.box-article .box-article__header {
  font-family: "Roboto", sans-serif;
  font-size: 20px;
}

.box-article {
  position: relative;
  width: 505px;
  height: 330px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.box-article__header {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
  padding: 10px 0 10px 0;
  width: 380px;
}
.box-article__description {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  padding: 10px 0 10px 0;
  max-width: 380px;
}
@media (min-width: 1200px) {
  .box-article {
    margin-top: 96px;
  }
}

.box-image {
  width: 505px;
  height: 330px;
}
.box-image img {
  max-width: 100%;
  max-height: 100%;
}
@media (min-width: 768px) {
  .box-image {
    margin-top: 96px;
  }
}

.btn {
  display: none;
  margin-top: 32px;
  color: #ffffff;
  font-weight: 700;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-size: 16px;
  line-height: 100%;
  order: 0;
  width: 128px;
  height: 48px;
  border-style: none;
  border-radius: 32px;
  background-color: #e4c1ae;
  display: block;
}
.btn:hover {
  background-color: #e1a98a;
  cursor: pointer;
}

.section-main__container {
  max-width: 1440px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .section-main__container {
    width: 100%;
    max-width: 750px;
  }
}
@media (min-width: 1200px) {
  .section-main__container {
    display: flex;
    flex-direction: row;
    width: 100%;
    max-width: 1440px;
  }
}

.section-main {
  width: 100%;
  display: flex;
  justify-content: center;
}